/**
 * The JobGroup allows creation and control of one or more dimensionality reduction jobs on 2-d data
 * Several job types are available:
 *  * dualTsne - starts a row and column embedding for the data
 *  * singleTsne - starts a tSNE job on row or colum as specified
 *  After creation one of the object or more subjobs can be added before start
 * tSNE embeddings are created using the DimRS service. One embedding is calculated
 * along the rows the other along the columns.
 * The class publishes pubsub events to indicate changes in overall status or
 * the completion of subjobs.
 *
 * The class publishes status change events using the pubsub-js system using a unique topic ID
 */
import Submitter from './submitter.js';
import rest from 'rest';
//import mime from 'rest/interceptor/mime';
//import errorCode from 'rest/interceptor/errorCode';
import defaultRequest from 'rest/interceptor/defaultRequest';
const uuidV1 = require('uuid/v1');

export default class JobGroup {

    static get EmbeddingDirection() { return {
        ROW: 0, //  Calculate the embedding in row direction
        COL: 1, //  Calculate embedding in column direction
    };}

    static get JobType() { return {
        TSNE: 0, //  sklearn tSNE
        ATSNE: 1, // approximated tSNE (Nicola Pezzotti)
    };}

    static get JobStatus() {return {
        CREATED: 'created', // job is just created
        QUEUED: 'queued', // DimRS job(s) haved been sent to the server
        RUNNING: 'running', // server has started processing the job(s)
        PROGRESS: 'progress', //Special status (optional) for progress updates - always pubsub-ed
        SUCCESS: 'success', // Execution succeeded the results can be obtained
        FAIL: 'failed', // The job failed
        CANCEL: 'canceled' // The job was cancelled
    };}


    static get default_tsne_params() { return {
        'n_jobs': 1,
        'n_iter': 1000,
        'n_components': 2,
        'perplexity': 30
    };}

    /**
     * Make a new DualtSNEJob
     * @param restURL
     * @param dataBuffer
     * @param tsne_params - optional object with n_jobs, n_iter, n_components, perplexity
     *      defaults are 1, 1000, 2 respectively, 30.
     */
    constructor(restURL) {
        this.restURL = restURL;
        this.submitClients = [];
        this.submitClients.push = (val) =>{
            this.submitClients[this.submitClients.length] = val;
            if (this.submitClients.length == 1) {
                this.status = JobGroup.JobStatus.QUEUED;
            }
        };
        this.pollClients = [];
        this.cancelFlags = [];
        this.running_urls = [];
        this.pubsubid = 'JOBGROUP_' + uuidV1();
        this.stat = JobGroup.JobStatus.CREATED;
        this.failmsg = undefined;
        this.resultURLs = [];
        this.resultCallerJobIds = [];
        this.jobsdone = 0;
        this.jobs = [];
        this.createdtime = Date();
    }

    get pubsubTopic() {
        return this.pubsubid;
    }

    get status() {
        return this.stat;
    }

    /**
     * Set the status - if the status changes from the previous value a PubSub event
     * is emitted
     * @param val - new status value
     */
    set status(val) {
        if (this.stat != val) {
            this.stat = val;
            PubSub.publish(this.pubsubTopic, {status: this.stat, jobsDone: this.jobsdone});
        }
    }

    get numSubJobs () {
        return this.jobs.length;
    }

    get numComplete () {
        return this.jobsdone;
    }

    get jobIds () {
        const jobIds = [];
        for (let job of this.jobs) {
            jobIds.push(job.callerJobId);
        }
        return jobIds;
    }

    /**
     * Change the number of completed jobs - will trigger a PubSub event
     * if the value is different from before. As a side effect the status may go to
     * RUNNING or SUCCESS ir appropriate.
     * @param val
     */
    set numComplete (val) {
        if (this.jobsdone != val) {
            this.jobsdone = val;
            if (this.jobsdone > 0 &&  this.jobsdone < this.numSubJobs && this.status != JobGroup.JobStatus.RUNNING) {
                this.status = JobGroup.JobStatus.RUNNING;
            }
            else if (this.jobsdone == this.numSubJobs) {
                this.status = JobGroup.JobStatus.SUCCESS;
            } else {
                PubSub.publish(this.pubsubTopic, {status: this.stat, jobsDone: this.jobsdone});
            }
        }
    }

    get failMessage() {
        return this.failmsg;
    }

    get results() {
        return this.resultURLs;
    }

    addJob(dataBuffer, jobType, callerJobId, tsne_params) {
        this.jobs.push({'type': jobType,
                        'buffer': dataBuffer,
                        'callerJobId': callerJobId,
                        'params': tsne_params || JobGroup.default_tsne_params}
        );
    }

    /**
     * Start the jobs in the group. Poll until they are finished.
     * Status updates will come via the pubsub
     */
    start() {
        let submitter = new Submitter(this.restURL);

        for (let job of this.jobs) {
            switch (job.type) {
            case JobGroup.JobType.TSNE:
                this.submitClients.push(submitter.tSNE(job.params, job.buffer, Submitter.JobDataType.NRRD, job.callerJobId));
                break;
            case JobGroup.JobType.ATSNE:
                this.submitClients.push(submitter.atSNE(job.params, job.buffer, job.callerJobId));
                break;
            default:
            }
        }
        // submit all of the configured jobs
        Promise.all(this.submitClients).then(values => {
            // Jobs are now queued and may already be running - poll clients are returned
            for (let val of values) {
                let client, url, callerJobId;
                [client, url, callerJobId] = val[1];
                this.running_urls.push(url); // urls save to permit cancel
                // wrap the poll clients to allow an event as each job completes
                let endJobPromise = new Promise((resolve, reject) => {
                    client.then(result => {
                        this.numComplete++;
                        resolve([callerJobId, result]);
                    }).catch(reason => {
                        this.failmsg = reason;
                        this.status = JobGroup.JobStatus.FAIL;
                        reject([callerJobId, reason]);
                    });
                });
                this.pollClients.push(endJobPromise);
            }
            // Jobs are now QUEUED/RUNNING
            this.status = JobGroup.JobStatus.RUNNING;
            // poll all of the configured jobs until finished or fail
            Promise.all(this.pollClients).then(values => {
                for (let val of values) {
                    this.resultCallerJobIds.push(val[0]);
                    this.resultURLs.push(val[1].entity.url);
                }
            }).catch (value => {
                this.failmsg = "Job: " + value[0] + " failed with: " + value[1];
                this.status = JobGroup.JobStatus.FAIL;
            });
        }).catch(reason => {
            this.failmsg = reason;
            this.status = JobGroup.JobStatus.FAIL;
        });

    }

    /**
     * Cancels by deleting the server jobs. Polling will then stop in response to the 404
     *
     * @param force - force the poll clients to terminate (useful if the server has died)
     */
    cancel(force) {
        let forced = force || false;
        // Deleting the jobs in the server will
        let deleteClients = [];
        for (let url of this.running_urls) {
            let client = rest.wrap(defaultRequest, {method: 'DELETE', path: url});
            deleteClients.push(client);
        }
        Promise.all(deleteClients).then(values => {
            console.log('Cancelled: ' + values);
            this.status = JobGroup.JobStatus.CANCEL;
        }).catch (reason => {
            console.log('Cancelled error: ' + reason);
            this.status = JobGroup.JobStatus.CANCEL;
        });
        if (forced) {
            // TODO come up with a method of also explicitly killing the poll clients
            // The polling will eventually stop due to the timeout.... but still
        }
    }

    /**
     * Return an thenable client that will contain an array of all nrrd results
     * in the same order that the jobs were submitted.
     */
    // The rest package (cujojs) does not seems to allow the overrideMimeType that
    // is needed to get the binary data i.e. 'arraybuffer'. So we make our own client.
    // see https://github.com/dcodeIO/protobuf.js/wiki/How-to-read-binary-data-in-the-browser-or-under-node.js%3F
    get_result() {
        let resultClients = [];
        for (let i = 0; i < this.resultURLs.length; i++) {
            let url = this.resultURLs[i];
            let callerJobId = this.resultCallerJobIds[i];
            let client =  new Promise(function (resolve, reject) {
                var request = new XMLHttpRequest();
                request.open('GET', url);
                request.responseType = 'arraybuffer';
                request.onload = function() {
                    if (this.status >= 200 && this.status < 300) {
                        var buf = request.response;
                        var bytes = new Uint8Array( buf );
                        resolve([callerJobId, bytes]);
                    }
                    else {
                        reject({
                            status: this.status,
                            statusText: request.statusText
                        });
                    }
                };
                request.onerror = function() {
                    reject({
                        status: this.status,
                        statusText: request.statusText
                    });
                };
                request.send();
            });
            resultClients.push(client);
        }
        return Promise.all(resultClients).then(values => {
            return values;
        });
    }


}
