import rest from 'rest';
import mime from 'rest/interceptor/mime';
import errorCode from 'rest/interceptor/errorCode';
import retry from 'rest/interceptor/retry';
import timeout from 'rest/interceptor/timeout';
import waitFor from './waitforinterceptor';

export default class Submitter {

    // enum used to indicate the type of data being passed
    static get JobDataType() { return {
        PICKLED: 0, //  A pickled example file from sklearn for example digits.pkl (used for testing')
        NRRD: 1,    // An nrrd file containing binary data
    };}

        // enum used to indicate the type of data being passed
    static get DataPointAxis() { return {
        ROW: 0, // data points are represented by rows - columns are dimensions
        COL: 1, // data points are represented by columns - rows are dimensions
    };}

    constructor(restURL) {
        this.restURL = restURL;
    }


    /**
     * Return a Promise that can be used to ping the DimRS service
     */
    ping() {
        return rest(this.restURL + '/ping').then(function(response) {
            if (response.status.code != 200) {
                return false;
            }
            return true;
        });
    }

    SNE(urlResource, tsneParams, data, datatype, callerJobId) { // eslint-disable-line no-unused-vars
        let form = new FormData();
        let blob = new Blob([data], {type: 'application/octet-stream'});
        Object.keys(tsneParams).forEach(function(v) {
            form.append(v, tsneParams[v]);
        });
        form.append('data_type', datatype);
        form.append('data', blob);
        let client = rest.wrap(mime, {mime: 'multipart/form-data'})
                         .wrap(errorCode);
        let self = this;
        const request = {
            method: 'POST',
            path: this.restURL + urlResource,
            entity: form,
        };
        return client(request).then(function(response) {
            //console.log('response: ', response);
            // Tried using response.header.location but this unreliable
            // possible due to redirect.
            let runningLocation = response.entity.url;
            return [true, self.pollJob(runningLocation, callerJobId)];
        });
    }

    /**
     * return a thenable client that will start a tSNE job
     * @param tsneParams - Object containing the parameters as defined in the rest interface
     * @param data - Float32Array data matrix on which to perform the tsne
     * @param datatype - SHould be one of Submitter.JobDataType
     */
    tSNE(tsneParams, data, datatype, callerJobId) { // eslint-disable-line no-unused-vars
        return this.SNE('/tSNE', tsneParams, data, datatype, callerJobId);
    }

    /**
     * return a thenable client that will start an a_tSNE job
     * @param tsneParams - Object containing the parameters as defined in the rest interface
     * @param data - Float32Array data matrix on which to perform the tsne
     * @param datatype - SHould be one of Submitter.JobDataType
     */
    atSNE(tsneParams, data, callerJobId) { // eslint-disable-line no-unused-vars
        return this.SNE('/a_tSNE', tsneParams, data, Submitter.JobDataType.NRRD, callerJobId);
    }

    /**
     * Return a thenable client that polls the url until the job is completed as
     * indicated by the properties in the response entity.
     * @param url url to poll
     */
    pollJob(url, callerJobId) {
        let requestTimeout = 5000; // 5 second timeout for initial request
        let pollDelay = 1000; // 1 second poll delay
        let retryTimeout = 1000000; // max expected job time approx 20 mins
        let client = rest.wrap(mime)
                 .wrap(timeout, {timeout: requestTimeout})
                 .wrap(errorCode) // reject actual errors
                 .wrap(waitFor, {waitForProperties: ['run_time', 'total_time', 'url']}) // retry until the result is found
                 .wrap(retry, {initial: pollDelay, multiplier: 1 })
                 .wrap(timeout, {timeout: retryTimeout});
        const request = {
            method: 'GET',
            path: url
        };
        return [client(request), url, callerJobId];
    }

}

