import rest from 'rest';
import defaultRequest from 'rest/interceptor/defaultRequest';

export default class Workers {
    static restart_workers(url) {
        return rest.wrap(defaultRequest, {method: 'DELETE', path: url + '/workers'});
    }

    static list_workers(url) {
        return rest.wrap(defaultRequest, {method: 'GET', path: url + '/workers'});
    }
}
